@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700,700i);
html {
  background: black;
  scrollbar-width: thin;
  scrollbar-color: black #404040; }

#App {
  display: flex;
  flex-direction: horizontal; }

.monitor {
  flex-grow: 1;
  position: relative; }

.network-text {
  color: white;
  position: absolute;
  top: 50%;
  left: 0;
  font-size: 2em;
  font-family: 'Roboto';
  width: 100%;
  text-align: center; }

.groups {
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
  padding: 1em; }

.group {
  color: white;
  font-family: 'Roboto', sans-serif;
  line-height: 1.75em; }
  @media (max-width: 1799px) {
    .group {
      font-size: 24px; } }
  @media (min-width: 1800px) {
    .group {
      font-size: 38px; } }

.green {
  background: lime; }

.red {
  background: #ff1414; }

.grey {
  background: #6f6f6f; }

.green-text {
  color: lime; }

.red-text {
  color: #ff1414; }

.grey-text {
  color: #6f6f6f; }

.square {
  display: inline-block;
  width: .8em;
  height: .8em;
  border: 0.1em solid rgba(0, 0, 0, 0.75); }

.circle {
  display: inline-block;
  position: absolute;
  bottom: 0;
  box-sizing: border-box;
  width: 1em;
  height: 1em;
  border: 0.1em solid rgba(0, 0, 0, 0.75);
  border-radius: 50%; }

.system .red.circle::before {
  content: '';
  position: absolute;
  width: 1.5em;
  height: 4em;
  left: -.35em;
  top: -1.5em;
  z-index: -1;
  opacity: 1;
  background: radial-gradient(closest-side, rgba(255, 0, 0, 0.5) 0%, rgba(255, 0, 0, 0) 100%);
  -webkit-animation-name: flashing;
          animation-name: flashing;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate; }

@-webkit-keyframes flashing {
  from {
    opacity: 0.5; }
  to {
    opacity: 1; } }

@keyframes flashing {
  from {
    opacity: 0.5; }
  to {
    opacity: 1; } }

.diamond {
  width: 0.15em;
  height: 0.15em;
  border: 0.09em solid rgba(0, 0, 0, 0.75);
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  top: 0.65em;
  left: 0.35em;
  position: absolute; }

.system {
  display: inline-block;
  position: relative;
  margin-left: .25em;
  margin-bottom: -.1em;
  min-width: 1em;
  height: 1.25em; }

.rects {
  position: absolute;
  display: flex;
  flex-direction: row;
  left: 0;
  bottom: 0;
  right: 0;
  width: 1em; }

.builds {
  margin-left: 1.1em;
  line-height: 1.55em; }

.build-viz {
  display: inline-block;
  position: relative; }

.build-viz:not(:last-child)::after {
  content: ', ';
  margin-right: .2em; }

.rect {
  flex: 1 1;
  height: .5em;
  box-sizing: border-box;
  border: 0.055em solid rgba(0, 0, 0, 0.75);
  border-top: 0.1em solid rgba(0, 0, 0, 0.75);
  border-bottom: 0.1em solid rgba(0, 0, 0, 0.75); }
  .rect:first-child {
    border-left: 0.1em solid rgba(0, 0, 0, 0.75); }
  .rect:last-child {
    border-right: 0.1em solid rgba(0, 0, 0, 0.75); }

.stats {
  color: white;
  font-family: 'Roboto', sans-serif;
  line-height: 1.75em; }
  @media (max-width: 1799px) {
    .stats {
      font-size: 24px; } }
  @media (min-width: 1800px) {
    .stats {
      font-size: 38px; } }
  .stats .circle, .stats .square {
    border: .15em solid black; }

.stat-line {
  color: black;
  text-align: center;
  padding: 0 1em; }
  .stat-line > * {
    position: relative;
    z-index: 1; }
  .stat-line .circle, .stat-line .square {
    position: relative;
    margin: 0 0.25em;
    bottom: -.125em; }

.stat-line-green {
  background: #85ff7a;
  position: relative; }
  .stat-line-green:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0.25;
    background: url("/domino.png");
    z-index: 0; }

.stat-line-grey {
  background: #BEBEBE;
  position: relative; }
  .stat-line-grey:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0.2;
    background: url("/domino.png");
    z-index: 0; }

.downed {
  background: #ff9999;
  position: relative;
  z-index: -1; }
  .downed:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0.25;
    background: url("/domino.png");
    z-index: 0; }

.downed-services {
  color: black;
  padding: 0 1em; }
  .downed-services .circle, .downed-services .square {
    position: relative;
    margin: 0 0.25em;
    bottom: -.55em;
    z-index: 1;
    border: .25em solid black; }
  .downed-services .circle {
    height: 1.75em;
    width: 1.75em; }
  .downed-services .square {
    height: 1.3em;
    width: 1.3em; }
  .downed-services .downed-service {
    padding-bottom: 1em; }
  .downed-services .downed-service-name, .downed-services .server-name {
    position: relative;
    display: inline-block;
    background: white;
    padding-left: 1.25em;
    padding-right: .5em;
    margin-left: -1em; }
  .downed-services .downed-service-name {
    font-weight: bold; }

.jenkins-log {
  background: black;
  padding: 0.25em 1em;
  overflow: hidden; }
  .jenkins-log h2 {
    margin: 0.25em 0; }

.log-line {
  position: relative;
  padding-left: 1em; }
  .log-line .diamond {
    font-size: 2em;
    top: 0.25em;
    left: 0; }

.indent {
  margin-left: 2em;
  position: relative; }
  .indent::before {
    content: "";
    border-left: .2em solid black;
    border-bottom: .2em solid black;
    position: absolute;
    width: 1.5em;
    height: 2.5em;
    left: -1em;
    bottom: .8em;
    z-index: 0; }

#network-status {
  position: absolute;
  top: .25em;
  right: .25em;
  width: 1em;
  height: 1em;
  box-sizing: border-box; }
  @media (max-width: 1799px) {
    #network-status {
      font-size: 75px; } }
  @media (min-width: 1800px) {
    #network-status {
      font-size: 75px; } }
  #network-status.waiting {
    cursor: pointer; }
  #network-status.waiting:hover::before {
    content: '';
    display: block;
    position: relative;
    width: .3em;
    height: .3em;
    left: calc(50% - .3em/2);
    top: calc(50% - .3em/2);
    background: darkred; }

.CircularProgressbar {
  position: absolute;
  top: 0; }

.loading {
  border-radius: 50%;
  border: .05em dashed lime;
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; }
  .loading .CircularProgressbar {
    display: none; }

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0turn);
            transform: rotate(0turn); }
  to {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn); } }

@keyframes spin {
  from {
    -webkit-transform: rotate(0turn);
            transform: rotate(0turn); }
  to {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn); } }

